.FooterTopRow {
  border-bottom: 1px solid var(--primary-gray);
}
.cs_footer_top_lable_group {
  color: var(--primary-gray);
  font-size: 1rem;
  font-weight: bold;
  padding:0!important;
}
.cs_footer_top_label {
  text-align: center;
  color: var(--primary-gray);
  margin-bottom: 0.5rem;
  margin-top: 0.5rem;
  padding: 0!important;
}
.cs_footer_top_label:hover {
  cursor: pointer;
  color: white !important;
  text-decoration: none;
}
